.back{
    background-image: url(../../../Media/background.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}
.gradient{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    content: '';
    opacity: .8;
    background: #000000;
    background: #207dff;
    background: -moz-linear-gradient(left, #207dff 0%, #a16ae8 100%);
    background: -webkit-gradient(left top, right top, color-stop(0%, #207dff), color-stop(100%, #a16ae8));
    background: -webkit-linear-gradient(left, #207dff 0%, #a16ae8 100%);
    background: -o-linear-gradient(left, #207dff 0%, #a16ae8 100%);
    background: -ms-linear-gradient(left, #207dff 0%, #a16ae8 100%);
    background: -webkit-gradient(linear, left top, right top, from(#207dff), to(#a16ae8));
    background: linear-gradient(to right, #207dff 0%, #a16ae8 100%);
}
@media (max-width: 991.98px){
.back-wrap {
    height: 900px;
}
}
.back-wrap {
    width: 100%;
    height: 600px;
    position: relative;
}
@media (min-width: 576px){
.container {
    max-width: 540px;
}
}

.container {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
}
.navContainer{
    background: transparent !important;
    position: absolute;
    top: 20px;
    left: 0;
    right: 0;
    z-index: 3;
}
.scrollContainer{
    margin-top: 0px;
    width: 100%;
    position: fixed !important;
    top: 0;
    left: 0;
    right: 0;
    z-index: 3;
    transition: .3s all ease-out;
}
.nav-button{
    font-size: 16px;
    padding-top: .9rem;
    padding-bottom: .9rem;
    padding-left: 20px;
    padding-right: 20px;
    color: #fff;
    font-weight: 400;
    opacity: 1 !important;
}
.hex{
    align-items: center;
    position: relative;
    color:white;
    height: 800px;
    padding-top: 100px;
}
.head1{
    color: rgba(255, 255, 255, 0.8);
    font-weight: 400;
}

@media (max-width: 991.98px){
.head2 {
    font-size: 50px;
}}
.head2 {
    font-size: 40px;
    color: #fff;
    line-height: 1.2;
    font-weight: 400;
}
.search-container{
    background: #fff;
    margin-top: -9px;
    margin: 5px;
}
.parent_scrollbar{
    position: relative;
    top: -100px;
    margin-left: auto;
    margin-right: auto;
}

  .slider .slick-slider .slick-arrow {
            width: 57px;
            opacity: 1;
            visibility: visible;
            border-radius: 2px;
            height: 141px;
            z-index: 1;
            background-color: #fff;
        }
        .slider .slick-slider  .slick-arrow :before {
                font-size: 50px;
                color: #747474;
            }

            .slider .slick-slider     .slick-arrow :hover {
                box-shadow: 0 3px 31px 0 rgba(0, 0, 0, 0.26);
            }
            .slider .slick-slider    .slick-arrow :before {
                    color: #000;
                }
            

                .slider .slick-slider    .slick-prev {
            left: 10px;
            opacity: 0.7;
        }
        .slider .slick-slider     .slick-prev :hover {
                opacity: 1;
            
        }

        .slider .slick-slider  .slick-next {
            right: 10px;
            opacity: 0.7;
        }
        .slider .slick-slider      .slick-next :hover {
                opacity: 1;
            }
.sliderA h4:before {
                content: '';
                width: 95px;
                height: 5px;
                background-color: #f44336;
                position: absolute;
                top: 104%;
                left: 20px;
            }
 .sliderA h4 {
                padding-left: 20px;
                position: relative;
