.image{
    position: absolute;
    left: 50%;
    top: 60.8%;
    transform: translateY(-50%) translateX(-50%);
    max-width: 275px;
    max-height: 275px;
    opacity: 1;
    transition: none;
}
.image_container{
    width: 487px;
    height: 275px;
    background-image: none;
    background-color: white;
   float: left;
    position: relative;
    height: 275px;
    background-position: center;
    background-size: 40px 40px;
    background-repeat: no-repeat;
}
.image_thumb{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateY(-50%) translateX(-50%);
    max-width: 40px;
    max-height: 40px;
    opacity: 1;
    transition: none;
}
.image_container_thumb{
    background-image: none;
    background-color: white;
    position: relative;
    margin-top: 10px;
    width: 56px;
    height: 56px;
    margin-right: 10px;
    border-radius: 3px;
    border: 1px solid #dadada;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}